import { Component } from 'preact';
import style from '../home/style.css';
import React from 'preact/compat';
import { useState } from 'preact/hooks';

export default class Github extends Component {
	state = {
		user: '',
		propUser: '',
		gitRepos: null,
		isLoading: false
	};
	loadGitData = user => {
		this.setState({ isLoading: true, gitRepos: null });
		fetch(`https://api.github.com/users/${user}`)
			.then(response => {
				if (response.status === 404) throw new Error();
				return response.json();
			})
			.then(user => {
				this.setState({ gitRepos: user.public_repos + '', isLoading: false });
			})
			.catch(() => this.setState({ gitRepos: 'err', isLoading: false }));
	};
	fetchUser = e => {
		e.preventDefault();
		this.loadGitData(this.state.user);
	};
	setUser = e => {
		this.setState({ user: e.target.value, gitRepos: null });
	};

	static getDerivedStateFromProps(nextProps, prevState) {
		if (nextProps.user !== prevState.propUser) {
			return {
				user: nextProps.user,
				propUser: nextProps.user,
				isLoading: false,
				gitRepos: null
			};
		}
		return null;
	}
	componentDidMount() {
		if (this.state.propUser) {
			this.loadGitData(this.state.propUser);
		}
	}
	componentDidUpdate(prevProps, prevState) {
		if (this.state.propUser && this.state.propUser !== prevState.propUser) {
			this.loadGitData(this.state.user);
		}
	}
	render({}, { user, gitRepos, isLoading }) {
		const repoText = <span> has {gitRepos} repositories on Github.</span>;
		const errText = <span> was not found!</span>;
		const [on, setOn] = useState(false);
		function toggle() { setOn(!on); }
		return (
			<div class={style.home}>
				<h1>Github account: {user ? user : '?!?'}</h1>
				<form onSubmit={this.fetchUser}>
					<label>
						<h4>
							Enter GitHub user:&nbsp;&nbsp;&nbsp;
							<input tabIndex={0}
								   type="text"
								   value={user} onChange={this.setUser}
							/>
							&nbsp;&nbsp;&nbsp;&nbsp;
							<button type="submit">Submit</button>
						</h4>
					</label>
				</form>
				{ !isLoading && !gitRepos && <h5>waiting for submit</h5> }
				{ isLoading && <h5>...fetching data...</h5> }
				{ gitRepos &&
					<h3>The user <span style={{ fontSize: 'larger' }}>{user}</span>
						{ gitRepos !== 'err' ?
							repoText
							: errText
						}
					</h3>
				}
				<button onClick={toggle}>Switch1!</button> - {on ? 'Is on!' : 'Is off!'} <br />
			</div>
		);
	}
}
