import { Component } from 'preact';
import { Router } from 'preact-router';

import Header from './header';

import Home from '../routes/home';
import Profile from '../routes/profile';
import Github from "../routes/github";
import React from "preact/compat";
import {useEffect} from "preact/hooks";

const divAndTextInTheMiddleOfScreen = {
	position: 'fixed',
	top: '50%',
	left: '50%',
	width: '30em',
	height: '18em',
	marginTop: '-9em', /*set to a negative number 1/2 of your height*/
	marginLeft: '-15em', /*set to a negative number 1/2 of your width*/
	lineHeight: '17em',
	textAlign: 'center',
	border: '1px solid #ccc',
	backgroundColor: '#444444'
};
export default class App extends Component {
	state = {
		url: '',
		appTitle: 'JavaExplained',
		title: 'JavaExplained',
		docTitle: ''
	};
	handleRoute = e => {
		this.setState( { url: e.url, title: e.current.props.title} );
		this.setState( {docTitle: this.state.appTitle + ' - ' + e.current.props.title} );
		if (e.current.props.user && e.current.props.user.includes('Jacek', 0)) {
			let author = 'Author profile';
			this.setState( {title: author, docTitle: this.state.appTitle + ' - ' + author} );
		}
	};
	render() {
		useEffect(() => {
			document.title = this.state.docTitle;
		}, [this.state.docTitle]);
		return (
			<div id="app">
				<Header title={this.state.title} />
				<Router onChange={this.handleRoute}>
					<Home path="/" title="Home" />
					<Profile path="/profile/:user?" title="User profile" />
					<Github path="/github/:user?" title="GitHub" />
					<div style={divAndTextInTheMiddleOfScreen} default>
						404! The page at <b>"{this.state.url}"</b> was not found!
					</div>
				</Router>
			</div>
		);
	}
}
